.blog-article {
  &__date {
    color: var(--color-grey-light);
  }

  &__img {
    width: 100%;
    height: 45rem;
    object-fit: cover;
    background-color: var(--color-grey-light-4);
    border-radius: 2.8rem;
    overflow: hidden;
  }

  &__paragraph::after {
    content: "";
    display: block;
    width: 20%;
    height: 2px;
    background-color: var(--color-grey-light);
    margin: 5.8rem auto;
  }
}
